html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

html,
body {
  position: relative;
  overflow-x: hidden;
}

body {}

ul,
ol,
li,
p,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
  list-style: none;
}

.btn-reset {
  text-decoration: none;
  color: white;
  border: none;
  background-color: transparent;

  &:hover {
    text-decoration: none;
    color: white;
    border: none;
  }
}

.top-title {
  text-align: center;
  color: #000000;
  @extend %lato;
  font-size: 44px;
  font-weight: 900;
  text-transform: uppercase;

  span {
    display: block;
    color: $yellow;
    @extend %lato;
    font-size: 35px;
    font-weight: 900;
    text-transform: uppercase;
  }
}

.btn-link {
  display: inline-block;
  color: #000000;
  @extend %bebas;
  font-size: 30px;
  font-weight: 700;
  line-height: 1;
  text-transform: uppercase;
  padding: 15px 33px 10px;
  background-color: $yellow;
  border-radius: 10px;
  transition: all 0.3s;

  &:hover {
    text-decoration: none;
    color: white;
    background-color: black;
  }
}