}

.top-title,
.top-title span,
.header__list a,
.cub__info .cub__phone,
.cub__info .cub__site,
.cub__subtitle,
.servies__item p,
.servies__title,
.servies__title span,
.form__inner input,
.form__inner input::placeholder,
.form__header-text,
.form-car,
.form-car::placeholder,
.tariffs__item-title,
.tariffs__item-subtitle,
.tariffs__item-price,
.best__item-title,
.best__item-subtitle,
.download__title,
.download__subtitle,
.testimonials__subtitle,
.testimonials__author,
.testimonials__profession,
.phone__subtitle,
.phone__number,
.footer__title,
.footer__subtitle,
.footer__download-link,
.footer__list a,
.footer__bottom-title {
  font-family: "Lato";
}

.btn-link,
.servies__link,
.form__servies button {
  font-family: "Bebas";
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

html,
body {
  position: relative;
  overflow-x: hidden;
}

ul,
ol,
li,
p,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
  list-style: none;
}

.btn-reset {
  text-decoration: none;
  color: white;
  border: none;
  background-color: transparent;
}

.btn-reset:hover {
  text-decoration: none;
  color: white;
  border: none;
}

.top-title {
  text-align: center;
  color: #000000;
  font-size: 44px;
  font-weight: 900;
  text-transform: uppercase;
}

.top-title span {
  display: block;
  color: #ffc61a;
  font-size: 35px;
  font-weight: 900;
  text-transform: uppercase;
}

.btn-link {
  display: inline-block;
  color: #000000;
  font-size: 30px;
  font-weight: 700;
  line-height: 1;
  text-transform: uppercase;
  padding: 15px 33px 10px;
  background-color: #ffc61a;
  border-radius: 10px;
  transition: all 0.3s;
}

.btn-link:hover {
  text-decoration: none;
  color: white;
  background-color: black;
}

.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100%;
}

.cub__wrapper {
  flex: 1 0 auto;
}

.header {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 100;
  width: 100%;
}

.header__inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 44px 74px 44px 64px;
}

.header__logo {
  position: relative;
  z-index: 100;
}

.header__list li {
  display: inline-block;
}

.header__list li:not(:last-child) {
  margin-right: 14px;
}

.header__list a {
  color: #ffffff;
  font-size: 17px;
  font-weight: 900;
  text-transform: uppercase;
  text-decoration: none;
  padding: 5px 15px;
  border-radius: 17px;
  cursor: pointer;
  transition: all 0.3s;
}

.header__list a:hover {
  color: #ffc61a;
}

.header__list a.active {
  color: black;
  background-color: #ffc61a;
  border-radius: 17px;
  padding: 5px 15px;
}

.header__burger {
  display: none;
  position: relative;
  z-index: 100;
  width: 40px;
  height: 31px;
  cursor: pointer;
}

.header__burger::before,
.header__burger::after {
  content: "";
  position: absolute;
  height: 2px;
  width: 100%;
  background-color: #ffc61a;
  transition: all 0.3s;
}

.header__burger span {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  height: 2px;
  width: 100%;
  background-color: #ffc61a;
  transition: all 0.3s;
  transform: scaleX(1);
}

.header__burger::before {
  top: 0;
}

.header__burger::after {
  bottom: 0;
}

.header__burger.active span {
  transform: scaleX(0);
}

.header__burger.active::before {
  transform: rotate(-45deg);
  top: 16px;
}

.header__burger.active::after {
  transform: rotate(45deg);
  bottom: 13px;
}

.cub {
  position: relative;
  background: url(../img/cub-bg.jpg) no-repeat center;
  background-size: cover;
  width: 100%;
  min-height: 772px;
}

.cub__car-img {
  position: absolute;
  z-index: 2;
  bottom: 120%;
  left: 50%;
  transform: translate(-50%, 120%);
  background-image: url(../img/car.png);
  width: 826px;
  height: 484px;
}

.cub__info {
  position: relative;
  z-index: 3;
  padding-top: 150px;
  text-align: center;
}

.cub__info .cub__phone {
  display: block;
  color: #ffffff;
  font-size: 72px;
  font-weight: 900;
  line-height: 1;
  text-transform: uppercase;
  margin-bottom: 21px;
}

.cub__info .cub__phone:hover {
  text-decoration: none;
}

.cub__info .cub__site {
  color: #ffffff;
  font-size: 20px;
  font-weight: 900;
  text-transform: uppercase;
  letter-spacing: 14.5px;
  line-height: 1;
}

.cub__subtitle {
  color: #ffc61a;
  font-size: 36px;
  font-weight: 400;
  text-transform: uppercase;
  line-height: 1;
  margin-bottom: 9px;
}

.servies {
  background-color: #ffc61a;
  padding: 151px 0 80px 0;
}

.servies__item:not(:last-child) {
  margin-bottom: 30px;
}

.servies__item p {
  color: black;
  font-size: 18px;
  font-weight: 400;
  margin-bottom: 60px;
}

.servies__title {
  color: black;
  font-size: 50px;
  font-weight: 900;
  text-transform: uppercase;
  margin-bottom: 44px;
}

.servies__title span {
  display: block;
  color: #000000;
  font-size: 35px;
  font-weight: 900;
  text-transform: uppercase;
  margin-bottom: 26px;
}

.servies__link {
  display: inline-block;
  color: #ffffff;
  font-size: 30px;
  font-weight: 700;
  text-transform: uppercase;
  background-color: black;
  border: 1px solid black;
  border-radius: 10px;
  padding: 10px 20px;
  cursor: pointer;
  transition: all 0.3s;
}

.servies__link:hover {
  color: black;
  background-color: #ffc61a;
  border: 1px solid black;
  border-radius: 10px;
}

.form__servies {
  position: relative;
  background-color: white;
  padding: 100px 36px 18px 36px;
}

.form__servies button {
  display: inline-block;
  color: #ffffff;
  font-size: 30px;
  font-weight: 400;
  line-height: 0.5;
  text-transform: uppercase;
  padding: 20px 10px 16px 10px;
  background-color: #131313;
  border: none;
  border-radius: 10px;
  width: 100%;
  text-align: center;
  margin-top: 40px;
  cursor: pointer;
  transition: all 0.3s;
}

.form__servies button:hover {
  transition: all 0.3s;
  color: black;
  background-color: #ffc61a;
}

.form__header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  background-color: black;
  padding: 15px 36px;
}

.form__inner input {
  color: black;
  font-size: 20px;
  font-weight: 400;
  padding: 12px 10px;
  border: none;
  border-bottom: 3px solid #e4e4e4;
  width: 100%;
  margin-bottom: 2px;
}

.form__inner input::placeholder {
  /* Style for "Phone   Ti" */
  width: 53px;
  height: 160px;
  color: #4b4b4b;
  font-size: 20px;
  font-weight: 400;
}

.form__inner input:focus {
  outline: 2px solid #ffc61a;
}

.form__header-text {
  color: #ffffff;
  font-size: 34px;
  font-weight: 900;
  text-transform: uppercase;
}

.form__header-text span {
  color: #ffc61a;
}

.form-car {
  color: black;
  font-size: 20px;
  font-weight: 400;
  padding: 15px 10px;
  border: none;
  border-bottom: 3px solid #e4e4e4;
  outline-color: #ffc61a;
  width: 100%;
}

.form-car::placeholder {
  /* Style for "Phone   Ti" */
  width: 53px;
  height: 160px;
  color: #4b4b4b;
  font-size: 20px;
  font-weight: 400;
}

.form-car:focus {
  outline: 2px solid #ffc61a;
}

.tariffs {
  padding: 63px 0 77px;
}

.tariffs__title {
  margin-bottom: 133px;
}

.tariffs__inner {
  margin: 0 -20px;
}

.tariffs__item {
  padding: 0 20px;
}

.tariffs__info {
  position: relative;
  text-align: center;
  background-color: #f5f4f2;
  padding: 124px 35px 25px;
  cursor: pointer;
  transition: all 0.3s;
}

.tariffs__info:hover {
  transition: all 0.3s;
  background-color: #ffc61a;
}

.tariffs__info:hover .tariffs__item-price {
  color: black;
}

.tariffs__info:hover .tariffs__item-link {
  background-color: black;
  color: white;
  text-decoration: none;
}

.tariffs__info:hover::after {
  content: "";
  position: absolute;
  right: 0;
  bottom: 0;
  background-image: url(../img/star.png);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  width: 74px;
  height: 74px;
}

.tariffs__info::before {
  content: "";
  position: absolute;
  top: -18%;
  left: 50%;
  transform: translateX(-50%);
  background-image: url(../img/car-2.png);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  width: 366px;
  height: 178px;
}

.tariffs__item-title {
  color: #000000;
  font-size: 30px;
  font-weight: 900;
  text-transform: uppercase;
  margin-bottom: 31px;
}

.tariffs__item-subtitle {
  color: black;
  font-size: 18px;
  font-weight: 400;
  margin-bottom: 35px;
}

.tariffs__item-price {
  color: #ffc61a;
  font-size: 44px;
  font-weight: 900;
  text-transform: uppercase;
  transition: all 0.3s;
}

.tariffs__item-link {
  margin-top: 35px;
}

.tariffs [data-aos^="flip"][data-aos^="flip"] {
  backface-visibility: hidden;
}

.best {
  background-image: url(../img/best-bg.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  padding: 81px 0 76px;
}

.best__title {
  margin-bottom: 102px;
  color: white;
}

.best__item {
  margin-bottom: 84px;
}

.best__item-inner {
  align-items: center;
}

.best .icon-star,
.best .icon-hourglass-end,
.best .icon-fort-awesome,
.best .icon-gear {
  color: #161616;
  font-size: 30px;
  padding: 20px;
  border-radius: 50%;
  background-color: #ffc61a;
}

.best__item-info {
  max-width: 366px;
  margin-left: auto;
}

.best__item-title {
  color: #ffc61a;
  font-size: 25px;
  font-weight: 900;
  text-transform: uppercase;
  margin-bottom: 24px;
}

.best__item-subtitle {
  color: #ffffff;
  font-size: 18px;
  font-weight: 400;
}

.best__link {
  position: relative;
  display: inline-block;
  left: 50%;
  transform: translateX(-50%);
  border: 1px solid #ffc61a;
}

.best__link:hover {
  border: 1px solid #ffc61a;
}

.download {
  position: relative;
  padding: 65px 0 0;
}

.download__top-title {
  position: relative;
  z-index: 5;
  margin-bottom: 81px;
  background-color: rgba(255, 255, 255, 0.4);
  border-radius: 50%;
}

.download__item {
  position: relative;
}

.download__item:first-child {
  margin-bottom: 30px;
}

.download__title {
  color: #000000;
  font-size: 25px;
  font-weight: 900;
  margin-bottom: 28px;
}

.download__subtitle {
  color: #101010;
  font-size: 18px;
  font-weight: 400;
  margin-bottom: 67px;
}

.download__button {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.download__link {
  display: inline-block;
}

.download__img {
  position: absolute;
  z-index: 1;
  top: -38%;
  max-width: 428px;
  max-height: 500px;
}

.download__img img {
  width: 100%;
  height: auto;
}

.testimonials {
  position: relative;
  z-index: 5;
  background-image: url(../img/testimonials-bg.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  padding: 110px 0 100px;
}

.testimonials__top-title {
  color: white;
  margin-bottom: 105px;
}

.testimonials__inner {
  justify-content: space-between;
}

.testimonials__item {
  background-color: #040707;
  padding: 40px 50px;
  border-radius: 50px;
}

.testimonials__subtitle {
  color: #ffffff;
  font-size: 18px;
  font-weight: 400;
  margin-bottom: 24px;
}

.testimonials__img {
  max-width: 74px;
  max-height: 74px;
}

.testimonials__img img {
  border-radius: 50%;
}

.testimonials__author {
  color: #ffc61a;
  font-size: 25px;
  font-weight: 900;
  text-transform: uppercase;
}

.testimonials__profession {
  /* Style for "Business m" */
  width: 132px;
  height: 14px;
  color: #ffffff;
  font-size: 18px;
  font-weight: 900;
  text-transform: uppercase;
}

.phone {
  background-color: #ffc61a;
  padding: 80px 0 80px;
  text-align: center;
}

.phone__subtitle {
  color: #000000;
  font-size: 35px;
  font-weight: 900;
  text-transform: uppercase;
}

.phone__number {
  display: inline-block;
  color: #000000;
  font-size: 77px;
  font-weight: 900;
  text-transform: uppercase;
  transition: all 0.3s;
  cursor: pointer;
}

.phone__number:hover {
  text-decoration: none;
  color: white;
}

.footer {
  flex: 0 0 auto;
  position: relative;
  background-image: url(../img/footer-bg.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

.footer__top {
  padding: 80px 0;
}

.footer__title {
  color: #ffffff;
  font-size: 25px;
  font-weight: 900;
  text-transform: uppercase;
  padding-bottom: 46px;
  border-bottom: 8px solid #ffc61a;
  margin-bottom: 46px;
}

.footer__subtitle {
  color: #ffffff;
  font-size: 18px;
  font-weight: 400;
  margin-bottom: 39px;
}

.footer__social-link {
  display: inline-block;
}

.footer__social-link:not(:last-child) {
  margin-right: 20px;
}

.footer__social-link:hover {
  text-decoration: none;
}

.footer .icon-facebook,
.footer .icon-google-plus,
.footer .icon-twitter,
.footer .icon-instagram {
  color: #ffc61a;
  font-size: 20px;
  cursor: pointer;
  transition: all 0.3s;
}

.footer .icon-facebook:hover,
.footer .icon-google-plus:hover,
.footer .icon-twitter:hover,
.footer .icon-instagram:hover {
  color: white;
}

.footer__download-link {
  display: block;
  color: #ffc61a;
  font-size: 20px;
  font-weight: 400;
  padding-right: 45px;
  cursor: pointer;
  transition: all 0.3s;
}

.footer__download-link:hover {
  color: white;
  text-decoration: none;
}

.footer__download-link:hover .icon-android,
.footer__download-link:hover .icon-apple {
  color: #ffc61a;
}

.footer__download-link:first-child {
  margin-bottom: 20px;
}

.footer__download-link .icon-android,
.footer__download-link .icon-apple {
  color: #ffffff;
  font-size: 30px;
  transition: all 0.3s;
}

.footer__download-link .icon-android {
  margin-left: 30px;
}

.footer__download-link .icon-apple {
  margin-left: 70px;
}

.footer__list li {
  max-width: 260px;
  margin-bottom: 30px;
}

.footer__list a {
  position: relative;
  display: block;
  padding-left: 30px;
  color: #ffffff;
  font-size: 18px;
  font-weight: 400;
  cursor: pointer;
  transition: all 0.3s;
}

.footer__list a:hover {
  color: #ffc61a;
  text-decoration: none;
}

.footer__list a:hover .icon__link {
  color: #ffc61a;
}

.footer__list .icon__link {
  position: absolute;
  top: 0;
  left: 0;
  color: #ffffff;
  text-align: center;
}

.footer__list .icon-map-marker {
  font-size: 18px;
}

.footer__list .icon-mobile {
  font-size: 24px;
}

.footer__list .icon-envelope {
  font-size: 16px;
}

.footer__list .icon-mouse-pointer {
  font-size: 18px;
}

.footer__bottom {
  position: absolute;
  z-index: 2;
  left: 0;
  bottom: 0;
  display: flex;
  align-items: center;
  text-align: center;
  width: 100%;
  height: 52px;
  background-color: rgba(0, 0, 0, 0.5);
}

.footer__bottom-title {
  position: relative;
  z-index: 3;
  color: #ffffff;
  font-size: 14px;
  font-weight: 400;
  letter-spacing: 0.7px;
}

.scroll-up {
  position: fixed;
  right: 50px;
  bottom: 50px;
  z-index: 1000;
  width: 50px;
  height: 50px;
  color: #dcdcdc;
  cursor: pointer;
  border-radius: 50%;
  box-shadow: inset 0 0 0 4px #dcdcdc;
  transition: all 0.3s;
  transform: translateY(25px);
  opacity: 0;
  visibility: hidden;
}

.scroll-up::before {
  content: 'â†‘';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 20px;
}

.scroll-up:hover {
  color: #ffc61a;
}

.scroll-up--active {
  visibility: visible;
  opacity: 1;
  transform: translateY(0);
}

.scroll-up__svg-path {
  box-sizing: border-box;
  fill: none;
  stroke: #ffc61a;
  stroke-width: 4px;
  transition: all 0.3s;
}

@media (max-width: 1200px) {
  .header__list li:not(:last-child) {
    margin-right: 0;
    margin-bottom: 30px;
  }

  .download__button {
    display: block;
  }

  .download__link {
    display: block;
    text-align: center;
  }

  .download__link:first-child {
    margin-bottom: 30px;
  }

  .download__img {
    top: 11%;
  }
}

@media (max-width: 992px) {
  .header__menu {
    position: fixed;
    z-index: 99;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: black;
    transform: translateY(-100%);
    height: 100vh;
    transition: all 0.3s;
  }

  .header__menu.active {
    transform: translateY(0%);
  }

  .header__list {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  .header__list li {
    display: block;
    text-align: center;
  }

  .header__list a {
    font-size: 20px;
  }

  .header__burger {
    display: block;
  }

  .cub__info .cub__phone {
    font-size: 60px;
  }

  .tariffs__item:not(:last-child) {
    margin-bottom: 120px;
  }

  .best__item-inner {
    justify-content: center;
  }

  .best__item-info {
    margin-left: 0;
  }

  .download__button {
    justify-content: space-around;
  }

  .testimonials__item {
    margin-bottom: 30px;
  }

  .phone__subtitle {
    font-size: 25px;
  }

  .phone__number {
    font-size: 37px;
  }

  .footer__item:not(:last-child) {
    margin-bottom: 30px;
  }

  html.block {
    overflow: hidden !important;
  }
}

@media (max-width: 768px) {
  .header__logo {
    max-width: 120px;
    max-height: 52;
  }

  .header__logo img {
    width: 100%;
    height: 100%;
  }
}

@media (max-width: 576px) {
  .header__inner {
    padding: 44px 15px 44px 15px;
  }

  .cub__info .cub__phone {
    font-size: 50px;
  }

  .cub__info .cub__site {
    font-size: 15px;
    letter-spacing: 4px;
  }

  .best__item-inner {
    text-align: center;
  }

  .best__item-img {
    margin-bottom: 40px;
  }

  .download__button {
    display: block;
  }

  .download__link {
    display: block;
    text-align: center;
  }

  .download__link:first-child {
    margin-bottom: 30px;
  }

  .testimonials__item {
    margin: 0 15px 30px;
  }
}

@media (max-width: 400px) {
  .cub__info .cub__phone {
    font-size: 30px;
  }

  .cub__subtitle {
    font-size: 20px;
  }
}