@import "font-face";
@import "fonts";
@import "vars";
@import "global";

//============================<<START CODE>>======================================
.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100%;
}

.cub__wrapper {
  flex: 1 0 auto;
}

//Header==========================================================================

.header {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 100;
  width: 100%;
  // .header__inner

  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 44px 74px 44px 64px;

    @media (max-width: 576px) {
      padding: 44px 15px 44px 15px;
    }
  }

  // .header__logo

  &__logo {
    position: relative;
    z-index: 100;

    @media (max-width: 768px) {
      max-width: 120px;
      max-height: 52;

      img {
        width: 100%;
        height: 100%;
      }
    }
  }

  // .header__menu

  &__menu {
    @media (max-width: 992px) {
      position: fixed;
      z-index: 99;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background-color: black;
      transform: translateY(-100%);
      height: 100vh;
      transition: all 0.3s;
    }

    &.active {
      @media (max-width: 992px) {
        transform: translateY(0%);
      }
    }
  }

  // .header__list

  &__list {
    @media (max-width: 992px) {
      position: fixed;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }

    li {
      display: inline-block;

      @media (max-width: 992px) {
        display: block;
        text-align: center;
      }

      &:not(:last-child) {
        margin-right: 14px;

        @media (max-width: 1200px) {
          margin-right: 0;
          margin-bottom: 30px;
        }
      }
    }

    a {
      color: #ffffff;
      @extend %lato;
      font-size: 17px;
      font-weight: 900;
      text-transform: uppercase;
      text-decoration: none;
      padding: 5px 15px;
      border-radius: 17px;
      cursor: pointer;
      transition: all 0.3s;

      @media (max-width: 992px) {
        font-size: 20px;
      }

      &:hover {
        color: $yellow;
      }

      &.active {
        color: black;
        background-color: $yellow;
        border-radius: 17px;
        padding: 5px 15px;
      }
    }
  }

  // .header__burger

  &__burger {
    display: none;

    @media (max-width: 992px) {
      display: block;
    }

    position: relative;
    z-index: 100;
    width: 40px;
    height: 31px;
    cursor: pointer;

    &::before,
    &::after {
      content: "";
      position: absolute;
      height: 2px;
      width: 100%;
      background-color: $yellow;
      transition: all 0.3s;
    }

    span {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      height: 2px;
      width: 100%;
      background-color: $yellow;
      transition: all 0.3s;
      transform: scaleX(1);
    }

    &::before {
      top: 0;
    }

    &::after {
      bottom: 0;
    }

    &.active {
      span {
        transform: scaleX(0);
      }

      &::before {
        transform: rotate(-45deg);
        top: 16px;
      }

      &::after {
        transform: rotate(45deg);
        bottom: 13px;
      }
    }
  }
}

//Header==========================================================================
//Cub==========================================================================

.cub {
  position: relative;
  background: url(../img/cub-bg.jpg) no-repeat center;
  background-size: cover;
  width: 100%;
  min-height: 772px;

  &__car-img {
    position: absolute;
    z-index: 2;
    bottom: 120%;
    left: 50%;
    transform: translate(-50%, 120%);
    background-image: url(../img/car.png);
    width: 826px;
    height: 484px;
  }

  // .cub__info

  &__info {
    position: relative;
    z-index: 3;
    padding-top: 150px;
    text-align: center;

    .cub__phone {
      display: block;
      color: #ffffff;
      @extend %lato;
      font-size: 72px;
      font-weight: 900;
      line-height: 1;
      text-transform: uppercase;
      margin-bottom: 21px;

      @media (max-width: 992px) {
        font-size: 60px;
      }

      @media (max-width: 576px) {
        font-size: 50px;
      }

      @media (max-width: 400px) {
        font-size: 30px;
      }

      &:hover {
        text-decoration: none;
      }
    }

    .cub__site {
      color: #ffffff;
      @extend %lato;
      font-size: 20px;
      font-weight: 900;
      text-transform: uppercase;
      letter-spacing: 14.5px;
      line-height: 1;

      @media (max-width: 576px) {
        font-size: 15px;
        letter-spacing: 4px;
      }
    }
  }

  // .cub__subtitle

  &__subtitle {
    color: $yellow;
    @extend %lato;
    font-size: 36px;
    font-weight: 400;
    text-transform: uppercase;
    line-height: 1;
    margin-bottom: 9px;

    @media (max-width: 400px) {
      font-size: 20px;
    }
  }
}

//Cub==========================================================================
//servies==========================================================================
.servies {
  background-color: $yellow;
  padding: 151px 0 80px 0;

  // .servies__item

  &__item {
    &:not(:last-child) {
      margin-bottom: 30px;
    }
    & p{
      color: black;
      @extend %lato;
      font-size: 18px;
      font-weight: 400;
      margin-bottom: 60px;
    }
  }

  // .servies__title

  &__title {
    color: black;
    @extend %lato;
    font-size: 50px;
    font-weight: 900;
    text-transform: uppercase;
    margin-bottom: 44px;

    span {
      display: block;
      color: #000000;
      @extend %lato;
      font-size: 35px;
      font-weight: 900;
      text-transform: uppercase;
      margin-bottom: 26px;
    }
  }


  // .servies__link

  &__link {
    display: inline-block;
    color: #ffffff;
    @extend %bebas;
    font-size: 30px;
    font-weight: 700;
    text-transform: uppercase;
    background-color: black;
    border: 1px solid black;
    border-radius: 10px;
    padding: 10px 20px;
    cursor: pointer;
    transition: all 0.3s;

    &:hover {
      color: black;
      background-color: $yellow;
      border: 1px solid black;
      border-radius: 10px;
    }
  }
}

.form {

  // .form__servies
  &__servies {
    position: relative;
    background-color: white;
    padding: 100px 36px 18px 36px;

    button {
      display: inline-block;
      color: #ffffff;
      @extend %bebas;
      font-size: 30px;
      font-weight: 400;
      line-height: 0.5;
      text-transform: uppercase;
      padding: 20px 10px 16px 10px;
      background-color: #131313;
      border: none;
      border-radius: 10px;
      width: 100%;
      text-align: center;
      margin-top: 40px;
      cursor: pointer;
      transition: all 0.3s;

      &:hover {
        transition: all 0.3s;
        color: black;
        background-color: $yellow;
      }
    }
  }

  // .form__header
  &__header {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    background-color: black;
    padding: 15px 36px;
  }

  // .form__Inner

  &__inner {
    input {
      color: black;
      @extend %lato;
      font-size: 20px;
      font-weight: 400;
      padding: 12px 10px;
      border: none;
      border-bottom: 3px solid #e4e4e4;
      width: 100%;
      margin-bottom: 2px;

      &::placeholder {
        /* Style for "Phone   Ti" */
        width: 53px;
        height: 160px;
        color: #4b4b4b;
        @extend %lato;
        font-size: 20px;
        font-weight: 400;
      }

      &:focus {
        outline: 2px solid $yellow;
      }
    }
  }

  // .form__header-text

  &__header-text {
    color: #ffffff;
    @extend %lato;
    font-size: 34px;
    font-weight: 900;
    text-transform: uppercase;

    span {
      color: $yellow;
    }
  }
}

.form-car {
  color: black;
  @extend %lato;
  font-size: 20px;
  font-weight: 400;
  padding: 15px 10px;
  border: none;
  border-bottom: 3px solid #e4e4e4;
  outline-color: $yellow;
  width: 100%;

  &::placeholder {
    /* Style for "Phone   Ti" */
    width: 53px;
    height: 160px;
    color: #4b4b4b;
    @extend %lato;
    font-size: 20px;
    font-weight: 400;
  }

  &:focus {
    outline: 2px solid $yellow;
  }
}

//servies==========================================================================
//Tariffs==========================================================================
.tariffs {
  padding: 63px 0 77px;
  // .tariffs__title

  &__title {
    margin-bottom: 133px;
  }

  // .tariffs__inner

  &__inner {
    margin: 0 -20px;
  }

  // .tariffs__item

  &__item {
    padding: 0 20px;

    @media (max-width: 992px) {
      &:not(:last-child) {
        margin-bottom: 120px;
      }
    }
  }

  // .tariffs__item
  &__info {
    position: relative;
    text-align: center;
    background-color: #f5f4f2;
    padding: 124px 35px 25px;
    cursor: pointer;
    transition: all 0.3s;

    &:hover {
      transition: all 0.3s;
      background-color: $yellow;

      .tariffs__item-price {
        color: black;
      }

      .tariffs__item-link {
        background-color: black;
        color: white;
        text-decoration: none;
      }

      &::after {
        content: "";
        position: absolute;
        right: 0;
        bottom: 0;
        background-image: url(../img/star.png);
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center;
        width: 74px;
        height: 74px;
      }
    }

    &::before {
      content: "";
      position: absolute;
      top: -18%;
      left: 50%;
      transform: translateX(-50%);
      background-image: url(../img/car-2.png);
      background-repeat: no-repeat;
      background-size: cover;
      background-position: center;
      width: 366px;
      height: 178px;
    }
  }

  // .tariffs__item-title

  &__item-title {
    color: #000000;
    @extend %lato;
    font-size: 30px;
    font-weight: 900;
    text-transform: uppercase;
    margin-bottom: 31px;
  }

  // .tariffs__item-subtitle

  &__item-subtitle {
    color: black;
    @extend %lato;
    font-size: 18px;
    font-weight: 400;
    margin-bottom: 35px;
  }

  // .tariffs__item-price

  &__item-price {
    color: $yellow;
    @extend %lato;
    font-size: 44px;
    font-weight: 900;
    text-transform: uppercase;
    transition: all 0.3s;
  }

  // .tariffs__item-link

  &__item-link {
    margin-top: 35px;
  }

  [data-aos^="flip"][data-aos^="flip"] {
    backface-visibility: hidden;
  }
}

//Tariffs==========================================================================
//Best==========================================================================
.best {
  background-image: url(../img/best-bg.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  padding: 81px 0 76px;

  // .best__title

  &__title {
    margin-bottom: 102px;
    color: white;
  }

  // .best__item

  &__item {
    margin-bottom: 84px;
  }

  // .best__item-inner

  &__item-inner {
    align-items: center;

    @media (max-width: 992px) {
      justify-content: center;
    }

    @media (max-width: 576px) {
      text-align: center;
    }
  }

  // .best__item-img

  &__item-img {
    @media (max-width: 576px) {
      margin-bottom: 40px;
    }
  }

  .icon-star,
  .icon-hourglass-end,
  .icon-fort-awesome,
  .icon-gear {
    color: #161616;
    font-size: 30px;
    padding: 20px;
    border-radius: 50%;
    background-color: #ffc61a;
  }

  // .best__item-info

  &__item-info {
    max-width: 366px;
    margin-left: auto;

    @media (max-width: 992px) {
      margin-left: 0;
    }
  }

  // .best__item-title

  &__item-title {
    color: $yellow;
    @extend %lato;
    font-size: 25px;
    font-weight: 900;
    text-transform: uppercase;
    margin-bottom: 24px;
  }

  // .best__item-subtitle

  &__item-subtitle {
    color: #ffffff;
    @extend %lato;
    font-size: 18px;
    font-weight: 400;
  }

  // .best__link
  &__link {
    position: relative;
    display: inline-block;
    left: 50%;
    transform: translateX(-50%);
    border: 1px solid $yellow;

    &:hover {
      border: 1px solid $yellow;
    }
  }
}

//Best==========================================================================
//Download==========================================================================
.download {
  position: relative;
  padding: 65px 0 0;
  // .download__top-title

  &__top-title {
    position: relative;
    z-index: 5;
    margin-bottom: 81px;
    background-color: rgba(255, 255, 255, 0.4);
    border-radius: 50%;
  }

  // .download__item

  &__item {
    position: relative;

    &:first-child {
      margin-bottom: 30px;
    }
  }

  // .download__title

  &__title {
    color: #000000;
    @extend %lato;
    font-size: 25px;
    font-weight: 900;
    margin-bottom: 28px;
  }

  // .download__subtitle

  &__subtitle {
    color: #101010;
    @extend %lato;
    font-size: 18px;
    font-weight: 400;
    margin-bottom: 67px;
  }

  // .download__button

  &__button {
    display: flex;
    justify-content: space-between;
    align-items: center;

    @media (max-width: 1200px) {
      display: block;
    }

    @media (max-width: 992px) {
      justify-content: space-around;
    }

    @media (max-width: 576px) {
      display: block;
    }
  }

  // .download__link

  &__link {
    display: inline-block;

    @media (max-width: 1200px) {
      display: block;
      text-align: center;

      &:first-child {
        margin-bottom: 30px;
      }
    }

    @media (max-width: 576px) {
      display: block;
      text-align: center;

      &:first-child {
        margin-bottom: 30px;
      }
    }
  }

  // .download__img

  &__img {
    position: absolute;
    z-index: 1;
    top: -38%;
    max-width: 428px;
    max-height: 500px;

    @media (max-width: 1200px) {
      top: 11%;
    }

    img {
      width: 100%;
      height: auto;
    }
  }
}

//Download==========================================================================
//Testimonials==========================================================================
.testimonials {
  position: relative;
  z-index: 5;
  background-image: url(../img/testimonials-bg.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  padding: 110px 0 100px;

  // .testimonials__top-title

  &__top-title {
    color: white;
    margin-bottom: 105px;
  }

  // .testimonials__inner

  &__inner {
    justify-content: space-between;
  }

  // .testimonials__item

  &__item {
    background-color: #040707;
    padding: 40px 50px;
    border-radius: 50px;

    @media (max-width: 992px) {
      margin-bottom: 30px;
    }

    @media (max-width: 576px) {
      margin: 0 15px 30px;
    }
  }

  // .testimonials__subtitle

  &__subtitle {
    color: #ffffff;
    @extend %lato;
    font-size: 18px;
    font-weight: 400;
    margin-bottom: 24px;
  }

  // .testimonials__img

  &__img {
    max-width: 74px;
    max-height: 74px;

    img {
      border-radius: 50%;
    }
  }

  // .testimonials__author

  &__author {
    color: $yellow;
    @extend %lato;
    font-size: 25px;
    font-weight: 900;
    text-transform: uppercase;
  }

  // .testimonials__profession

  &__profession {
    /* Style for "Business m" */
    width: 132px;
    height: 14px;
    color: #ffffff;
    @extend %lato;
    font-size: 18px;
    font-weight: 900;
    text-transform: uppercase;
  }
}

//Testimonials==========================================================================
//Phone==========================================================================
.phone {
  background-color: $yellow;
  padding: 80px 0 80px;
  text-align: center;

  // .phone__subtitle

  &__subtitle {
    color: #000000;
    @extend %lato;
    font-size: 35px;
    font-weight: 900;
    text-transform: uppercase;

    @media (max-width: 992px) {
      font-size: 25px;
    }
  }

  // .phone__number

  &__number {
    display: inline-block;
    color: #000000;
    @extend %lato;
    font-size: 77px;
    font-weight: 900;
    text-transform: uppercase;
    transition: all 0.3s;
    cursor: pointer;

    &:hover {
      text-decoration: none;
      color: white;
    }

    @media (max-width: 992px) {
      font-size: 37px;
    }
  }
}

//Phone==========================================================================
//Footer==========================================================================
.footer {
  flex: 0 0 auto;
  position: relative;
  background-image: url(../img/footer-bg.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  // .footer__top

  &__top {
    padding: 80px 0;
  }

  // .footer__item

  &__item {
    @media (max-width: 992px) {
      &:not(:last-child) {
        margin-bottom: 30px;
      }
    }
  }

  // .footer__title

  &__title {
    color: #ffffff;
    @extend %lato;
    font-size: 25px;
    font-weight: 900;
    text-transform: uppercase;
    padding-bottom: 46px;
    border-bottom: 8px solid $yellow;
    margin-bottom: 46px;
  }

  // .footer__subtitle

  &__subtitle {
    color: #ffffff;
    @extend %lato;
    font-size: 18px;
    font-weight: 400;
    margin-bottom: 39px;
  }

  // .footer__social-link

  &__social-link {
    display: inline-block;

    &:not(:last-child) {
      margin-right: 20px;
    }

    &:hover {
      text-decoration: none;
    }
  }

  .icon-facebook,
  .icon-google-plus,
  .icon-twitter,
  .icon-instagram {
    color: $yellow;
    font-size: 20px;
    cursor: pointer;
    transition: all 0.3s;

    &:hover {
      color: white;
    }
  }

  // .footer__download-link

  &__download-link {
    display: block;
    color: $yellow;
    @extend %lato;
    font-size: 20px;
    font-weight: 400;
    padding-right: 45px;
    cursor: pointer;
    transition: all 0.3s;

    &:hover {
      color: white;
      text-decoration: none;

      .icon-android,
      .icon-apple {
        color: $yellow;
      }
    }

    &:first-child {
      margin-bottom: 20px;
    }

    .icon-android,
    .icon-apple {
      color: #ffffff;
      font-size: 30px;
      transition: all 0.3s;
    }

    .icon-android {
      margin-left: 30px;
    }

    .icon-apple {
      margin-left: 70px;
    }
  }

  // .footer__list

  &__list {
    li {
      max-width: 260px;
      margin-bottom: 30px;
    }

    a {
      position: relative;
      display: block;
      padding-left: 30px;
      color: #ffffff;
      @extend %lato;
      font-size: 18px;
      font-weight: 400;
      cursor: pointer;
      transition: all 0.3s;

      &:hover {
        color: $yellow;
        text-decoration: none;

        .icon__link {
          color: $yellow;
        }
      }
    }

    .icon__link {
      position: absolute;
      top: 0;
      left: 0;
      color: #ffffff;
      text-align: center;
    }

    .icon-map-marker {
      font-size: 18px;
    }

    .icon-mobile {
      font-size: 24px;
    }

    .icon-envelope {
      font-size: 16px;
    }

    .icon-mouse-pointer {
      font-size: 18px;
    }
  }

  // .footer__bottom

  &__bottom {
    position: absolute;
    z-index: 2;
    left: 0;
    bottom: 0;
    display: flex;
    align-items: center;
    text-align: center;
    width: 100%;
    height: 52px;
    background-color: rgba(0, 0, 0, 0.5);
  }

  // .footer__bottom-title

  &__bottom-title {
    position: relative;
    z-index: 3;
    color: #ffffff;
    @extend %lato;
    font-size: 14px;
    font-weight: 400;
    letter-spacing: 0.7px;
  }
}

//Footer==========================================================================

.scroll-up {
  position: fixed;
  right: 50px;
  bottom: 50px;
  z-index: 1000;
  width: 50px;
  height: 50px;
  color: #dcdcdc;
  cursor: pointer;
  border-radius: 50%;
  box-shadow: inset 0 0 0 4px #dcdcdc;
  transition: all 0.3s;
  transform: translateY(25px);
  opacity: 0;
  visibility: hidden;
  &::before{
    content: 'â†‘';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 20px;
  }
  &:hover{
    color: $yellow;
  }

  &--active{
    visibility: visible;
    opacity: 1;
    transform: translateY(0);
  }

  &__svg-path{
    box-sizing: border-box;
    fill: none;
    stroke: $yellow;
    stroke-width: 4px ;
    transition: all 0.3s ;
  }
}

//============================<<END CODE>>========================================

html.block {
  @media (max-width: 992px) {
    overflow: hidden !important;
  }
}